/*::::::::
Internal Data
::::::::*/
var Support = false;

/*::::::::
General Event Handle
::::::::*/
function EventHandle_NotSupport() {
	console.error("%cJavaScript Macro - Storage", "font-weight: bold;", "\n", "Storage is not enabled/supported in this browser!");
};
function EventHandle_InvalidKey(Method, Key) {
	console.error("%cJavaScript Macro - Storage", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string!");
};
function EventHandle_InvalidKeyArray(Method, Key) {
	console.error("%cJavaScript Macro - Storage", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string! Pressed \"undefined\" as part of the result.");
};

/*::::::::
Determine Support Status
::::::::*/
if (localStorage) {
	Support = true;
};
function Status() {
	return Support;
};

/*::::::::
Set
::::::::*/
function Set(Key, Value = null) {
	if (Support != true) {
		EventHandle_NotSupport();
		return;
	};
	if (typeof Key === "string") {
		try {
			localStorage.setItem(Key, Value);
		} catch (Error) {
			console.error("%cJavaScript Macro - Storage", "font-weight: bold;", "\n", Error);
		};
	} else {
		EventHandle_InvalidKey("Set", Key);
	};
};

/*::::::::
Get
::::::::*/
function Get_Internal(Key) {
	return localStorage.getItem(Key);
}
function Get(Key) {
	if (Support != true) {
		EventHandle_NotSupport();
		return undefined;
	};
	if (typeof Key === "undefined") {
		let StoragePair = {};
		let StoragePairCount = localStorage.length;
		if (StoragePairCount > 0) {
			for (let index = 0; index < StoragePairCount; index++) {
				let StoragePair_Key = localStorage.key(index);
				StoragePair[StoragePair_Key] = Get_Internal(StoragePair_Key);
			};
		};
		return StoragePair;
	} else if (typeof Key === "string") {
		return Get_Internal(Key);
	} else if (Array.isArray(Key) === true) {
		let ValueArray = [];
		for (let index = 0; index < Key.length; index++) {
			if (typeof Key[index] === "string") {
				ValueArray.push(Get_Internal(Key[index]));
			} else {
				EventHandle_InvalidKeyArray("Get", Key[index]);
				ValueArray.push(undefined);
			};
		};
		return ValueArray;
	} else {
		EventHandle_InvalidKey("Get", Key);
		return undefined;
	};
};

/*::::::::
Delete
::::::::*/
function Delete_Internal(Key) {
	localStorage.removeItem(Key);
};
function Delete(Key) {
	if (Support != true) {
		EventHandle_NotSupport();
		return;
	};
	if (typeof Key === "string") {
		Delete_Internal(Key);
	} else if (Array.isArray(Key) === true) {
		for (let index = 0; index < Key.length; index++) {
			if (typeof Key[index] === "string") {
				Delete_Internal(Key[index]);
			} else {
				EventHandle_InvalidKeyArray("Delete", Key[index]);
			};
		};
	} else {
		EventHandle_InvalidKey("Delete", Key);
	};
};
const Del = Delete;
function DeleteAll() {
	if (Support != true) {
		EventHandle_NotSupport();
		return;
	};
	localStorage.clear();
};
const DelAll = DeleteAll;

/*::::::::
Package Module
::::::::*/
export {
	Status,
	Set,
	Get,
	Delete,
	Del,
	DeleteAll,
	DelAll
};
