/*::::::::
Internal Data
::::::::*/
var Support = false;
const CookieConfiguration = {
	"ExpireDuration": 365.25 * 24 * 60 * 60,
	"Domain": location.hostname,
	"Secure": false
};

/*::::::::
General Event Handle
::::::::*/
function EventHandle_NotSupport() {
	console.error("%cJavaScript Macro - Cookie", "font-weight: bold;", "\n", "Cookie is not enabled/supported in this browser!");
};
function EventHandle_InvalidKey(Method, Key) {
	console.error("%cJavaScript Macro - Cookie", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string!");
};
function EventHandle_InvalidKeyArray(Method, Key) {
	console.error("%cJavaScript Macro - Cookie", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string! Pressed \"undefined\" as part of the result.");
};

/*::::::::
Support Status
::::::::*/
if (navigator.cookieEnabled == true) {
	Support = true;
};
function Status() {
	return Support;
};

/*::::::::
Determine Secure
::::::::*/
if (location.protocol == "https:") {
	CookieConfiguration["Secure"] = true;
};

/*::::::::
Expire Duration
::::::::*/
function ExpireDuration(Input) {
	if (typeof Input === "undefined") {
		return CookieConfiguration["ExpireDuration"];
	} else if (Number.isSafeInteger(Input) === true && Input > 0) {
		CookieConfiguration["ExpireDuration"] = Input;
	} else {
		console.error("%cJavaScript Macro - Cookie", "font-weight: bold;", "\n", "Invalid expire duration \"" + JSON.stringify(Input) + "\"! Expire duration must be type of number, safe integer, and larger than 0!");
	};
};

/*::::::::
Internal Function
::::::::*/
function Cookie_Internal(Key, Value, Decay) {
	let ExpireTime;
	if (Decay === true || Decay === "Decay") {
		let OldTime = new Date(0);
		ExpireTime = OldTime.toUTCString();
	} else {
		let CurrentTime = new Date();
		CurrentTime.setTime(CurrentTime.getTime() + (CookieConfiguration["ExpireDuration"] * 1000));
		ExpireTime = CurrentTime.toUTCString();
	};
	let FullCookie = encodeURIComponent(Key) + "=" + encodeURIComponent(Value) + "; expires=" + ExpireTime + "; domain=" + CookieConfiguration["Domain"] + "; path=/";
	if (CookieConfiguration["Secure"] === true) {
		FullCookie += "; secure";
	};
	try {
		document.cookie = FullCookie;
	} catch (Error) {
		console.error("%cJavaScript Macro - Cookie", "font-weight: bold;", "\n", Error);
	};
};

/*::::::::
Set
::::::::*/
function Set(Key, Value = null) {
	if (Support != true) {
		EventHandle_NotSupport();
		return;
	};
	if (typeof Key === "string" && (typeof Value === "string" || typeof Value === "number" || typeof Value === "boolean")) {
		Cookie_Internal(Key, Value, false);
	} else {
		if (typeof Key !== "string") {
			EventHandle_InvalidKey("Set", Key);
		};
		if (typeof Value !== "string" && typeof Value !== "number" && typeof Value !== "boolean") {
			console.error("%cJavaScript Macro - Cookie", "font-weight: bold;", "\n", "Invalid value \"" + JSON.stringify(Value) + "\" in \"Set\" request! Value must be type of string/number/boolean!");
		};
	};
};

/*::::::::
Get
::::::::*/
function Get(Key) {
	if (Support != true) {
		EventHandle_NotSupport();
		return undefined;
	};
	let CookieList = document.cookie;
	let CookiePair = {};
	if (CookieList !== "") {
		CookieList = CookieList.split(";");
		for (let index = 0; index < CookieList.length; index++) {
			let CookiePiece = CookieList[index].split("=");
			while (CookiePiece[0].charAt(0) === " ") {
				CookiePiece[0] = CookiePiece[0].substring(1);
			};
			CookiePair[decodeURIComponent(CookiePiece[0])] = decodeURIComponent(CookiePiece[1]);
		};
	};
	if (typeof Key === "undefined") {
		return CookiePair;
	} else if (typeof Key === "string") {
		return CookiePair[Key];
	} else if (Array.isArray(Key) === true) {
		let ValueArray = [];
		for (let index = 0; index < Key.length; index++) {
			if (typeof Key[index] === "string") {
				ValueArray.push(CookiePair[Key[index]]);
			} else {
				EventHandle_InvalidKeyArray("Get", Key[index]);
				ValueArray.push(undefined);
			};
		};
		return ValueArray;
	} else {
		EventHandle_InvalidKey("Get", Key);
		return undefined;
	};
};

/*::::::::
Delete
::::::::*/
function Delete(Key) {
	if (Support != true) {
		EventHandle_NotSupport();
		return;
	};
	if (typeof Key === "string") {
		Cookie_Internal(Key, 0, true);
	} else if (Array.isArray(Key) === true) {
		for (let index = 0; index < Key.length; index++) {
			if (typeof Key[index] === "string") {
				Cookie_Internal(Key[index], 0, true);
			} else {
				EventHandle_InvalidKeyArray("Delete", Key[index]);
			};
		};
	} else {
		EventHandle_InvalidKey("Delete", Key);
	};
};
const Del = Delete;
function DeleteAll() {
	if (Support != true) {
		EventHandle_NotSupport();
		return;
	};
	let KeyArray = Object.keys(Get());
	for (let index = 0; index < KeyArray.length; index++) {
		Cookie_Internal(KeyArray[index], 0, true);
	};
};
const DelAll = DeleteAll;

/*::::::::
Rebirth
::::::::*/
function Rebirth() {
	if (Support != true) {
		EventHandle_NotSupport();
		return;
	};
	let CookiePair = Get();
	let KeyArray = Object.keys(CookiePair);
	let ValueArray = Object.values(CookiePair);
	for (let index = 0; index < KeyArray.length; index++) {
		Set(KeyArray[index], ValueArray[index]);
	};
};

/*::::::::
Package Module
::::::::*/
export {
	Status,
	ExpireDuration,
	Set,
	Get,
	Delete,
	Del,
	DeleteAll,
	DelAll,
	Rebirth
};
