/* Version */
function Version(Semantic) {
	if (typeof Semantic === "undefined") {
		return LibraryVersion;
	} else {
		if (typeof Semantic === "string") {
			switch (Semantic) {
				case "Major": case "major": case "MAJOR":
					Semantic = 0;
					break;
				case "Minor": case "minor": case "MINOR":
					Semantic = 1;
					break;
				case "Patch": case "patch": case "PATCH":
					Semantic = 2;
					break;
				default:
					break;
			};
		};
		let LibraryVersion_Split = LibraryVersion.split(".");
		if (Number.isInteger(Semantic) === true && Semantic >= 0 && Semantic < LibraryVersion_Split.length) {
			return LibraryVersion_Split[Semantic];
		} else {
			console.warn("%cJavaScript Macro - Version", "font-weight: bold;", "\n", "Invalid version semantic \"" + JSON.stringify(Semantic) + "\"! Version semantic must be type of number, integer, and between 0 and " + (LibraryVersion_Split.length - 1) + "!");
			return undefined;
		};
	};
};
