/*::::::::
General Event Handle
::::::::*/
function EventHandle_InvalidInputValue(ConverterName, InputValue, InputValueType) {
	console.error("%cJavaScript Macro - Converter", "font-weight: bold;", "\n", "Invalid input value \"" + JSON.stringify(InputValue) + "\" in the \"" + ConverterName + "\" converter! Input value must be type of " + InputValueType + "!");
};
function EventHandle_InvalidInputUnit(ConverterName, InputUnit) {
	console.error("%cJavaScript Macro - Converter", "font-weight: bold;", "\n", "Invalid input unit \"" + JSON.stringify(InputUnit) + "\" in the \"" + ConverterName + "\" converter! Input unit must be a standard unit!");
};
function EventHandle_InvalidOutputUnit(ConverterName, OutputUnit) {
	console.error("%cJavaScript Macro - Converter", "font-weight: bold;", "\n", "Invalid output unit \"" + JSON.stringify(OutputUnit) + "\" in the \"" + ConverterName + "\" converter! Output unit must be a standard unit!");
};

/*::::::::
Temperature
::::::::*/
function Temperature(InputValue, InputUnit, OutputUnit = "K") {
	let SIValue, OutputValue;
	let Invalid = false;
	if (typeof InputValue !== "number") {
		EventHandle_InvalidInputValue("Temperature", InputValue, "number");
		Invalid = true;
		InputValue = 1;
	};
	switch (InputUnit) {
		case "K": case "Kelvin": // SIUnit
			SIValue = InputValue;
			break;
		case "C": case "°C": case "℃": case "Celsius":
			SIValue = InputValue + 273.15;
			break;
		case "F": case "°F": case "℉": case "Fahrenheit":
			SIValue = (InputValue + 459.67) / 1.8;
			break;
		case "R": case "°R": case "Rankine":
			SIValue = InputValue / 1.8;
			break;
		case "De": case "°De": case "Delisle":
			SIValue = 373.15 - InputValue / 1.5;
			break;
		case "N": case "°N": case "Newton":
			SIValue = InputValue / 0.33 + 273.15;
			break;
		case "Re": case "°Re": case "Ré": case "°Ré": case "Réaumur": case "Reaumur":
			SIValue = InputValue * 1.25 + 273.15;
			break;
		case "Ro": case "°Ro": case "Rø": case "°Rø": case "Rømer": case "Romer":
			SIValue = (InputValue - 7.5) / 0.525 + 273.15;
			break;
		default:
			EventHandle_InvalidInputUnit("Temperature", InputUnit);
			Invalid = true;
			SIValue = 1;
			break;
	};
	switch (OutputUnit) {
		case "K": case "Kelvin": // SIUnit
			OutputValue = SIValue;
			break;
		case "C": case "°C": case "℃": case "Celsius":
			OutputValue = SIValue - 273.15;
			break;
		case "F": case "°F": case "℉": case "Fahrenheit":
			OutputValue = SIValue * 1.8 - 459.67;
			break;
		case "R": case "°R": case "Rankine":
			OutputValue = SIValue * 1.8;
			break;
		case "De": case "°De": case "Delisle":
			OutputValue = (373.15 - SIValue) * 1.5;
			break;
		case "N": case "°N": case "Newton":
			OutputValue = (SIValue - 273.15) * 0.33;
			break;
		case "Re": case "°Re": case "Ré": case "°Ré": case "Réaumur": case "Reaumur":
			OutputValue = (SIValue - 273.15) * 0.8;
			break;
		case "Ro": case "°Ro": case "Rø": case "°Rø": case "Rømer": case "Romer":
			OutputValue = (SIValue - 273.15) * 0.525 + 7.5;
			break;
		default:
			EventHandle_InvalidOutputUnit("Temperature", OutputUnit);
			Invalid = true;
			break;
	};
	if (Invalid == true) {
		OutputValue = undefined;
	};
	return OutputValue;
};

