/*::::::::
Addition
::::::::*/
Math.addition = function () {
	let List = [];
	if (Array.isArray(arguments[0]) === true) {
		List = arguments[0];
	} else {
		for (let index = 0; index < arguments.length; index++) {
			List.push(arguments[index]);
		};
	};
	if (List.length == 0) {
		console.error("Math.addition: No input!");
		return undefined;
	} else {
		let Invalid = false;
		for (let index = 0; index < List.length; index++) {
			if (typeof List[index] !== "number") {
				console.error("Math.addition: \"" + JSON.stringify(List[index]) + "\" is not a number!");
				Invalid = true;
			};
		};
		if (Invalid == true) {
			return undefined;
		};
	};
	let Total = List[0];
	if (List.length > 1) {
		for (let index = 1; index < List.length; index++) {
			Total = Total + List[index];
		};
	};
	return Total;
};
Math.add = Math.addition;

