/*::::::::
Virtual Memory Pool
::::::::*/
const Pool = {};

/*::::::::
General Event Handle
::::::::*/
function EventHandle_InvalidKey(Method, Key) {
	console.error("%cJavaScript Macro - Virtual Memory", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string!");
};
function EventHandle_InvalidKeyArray(Method, Key) {
	console.error("%cJavaScript Macro - Virtual Memory", "font-weight: bold;", "\n", "Invalid key \"" + JSON.stringify(Key) + "\" in \"" + Method + "\" request! Key must be type of string! Pressed \"undefined\" as part of the result.");
};

/*::::::::
Set
::::::::*/
function Set(Key, Value = null) {
	if (typeof Key === "string") {
		Pool[Key] = Value;
	} else {
		EventHandle_InvalidKey("Set", Key);
	};
};

/*::::::::
Get
::::::::*/
function Get_Internal(Key) {
	return Pool[Key];
};
function Get(Key) {
	if (typeof Key === "undefined") {
		return Pool;
	} else if (typeof Key === "string") {
		return Get_Internal(Key);
	} else if (Array.isArray(Key) === true) {
		let ValueArray = [];
		for (let index = 0; index < Key.length; index++) {
			if (typeof Key[index] === "string") {
				ValueArray.push(Get_Internal(Key[index]));
			} else {
				EventHandle_InvalidKeyArray("Get", Key[index]);
				ValueArray.push(undefined);
			};
		};
		return ValueArray;
	} else {
		EventHandle_InvalidKey("Get", Key);
		return undefined;
	};
};

/*::::::::
Delete
::::::::*/
function Delete_Internal(Key) {
	delete Pool[Key];
};
function Delete(Key) {
	if (typeof Key === "string") {
		Delete_Internal(Key);
	} else if (Array.isArray(Key) === true) {
		for (let index = 0; index < Key.length; index++) {
			if (typeof Key[index] === "string") {
				Delete_Internal(Key[index]);
			} else {
				EventHandle_InvalidKeyArray("Delete", Key[index]);
			};
		};
	} else {
		EventHandle_InvalidKey("Delete", Key);
	};
};
const Del = Delete;
function DeleteAll() {
	let KeyArray = Object.keys(Pool);
	for (let index = 0; index < KeyArray.length; index++) {
		Delete_Internal(KeyArray[index]);
	};
};
const DelAll = DeleteAll;

/*::::::::
Package Module
::::::::*/
export {
	Set,
	Get,
	Delete,
	Del,
	DeleteAll,
	DelAll
};
